import Users from './index';
import {connect} from "react-redux";
import {useEffect} from "react";
import {compose} from "redux";
import {
    updateSearchData,
    toggleFollow,
    fetchUsers
} from '../../redux/users/reducer';
import {
    getFollowingInProgress,
    getFriends,
    getLoading,
    getSearchData,
    getUsers
} from "../../redux/users/selectors";

const UsersAPIContainer = props => {
    let {
        searchData,
        fetchUsers,
        updateSearchData,
        setCurrentPage,
        location: {search},
        ...other
    } = props;

    const {page, count, term} = searchData;

    useEffect(()=> {
        const searchPage = Number(new URLSearchParams(search).get('page')) || 1;
        const searchQuery = new URLSearchParams(search).get('term') || '';

        if(Number(searchPage) !== page || searchQuery !== term) {
            updateSearchData({
                term: searchQuery,
                page: searchPage
            })
        }
        else {
            fetchUsers({page, count, term});
        }

    }, [page, count, term, search, updateSearchData, fetchUsers]);

    return Users({searchData: {...searchData}, ...other});
};

export default compose(
    connect(
        state => ({
            loading: getLoading(state),
            users: getUsers(state),
            friends: getFriends(state),
            searchData: getSearchData(state),
            followingInProgress: getFollowingInProgress(state),
            myId: state.authData.id,
            isAuth: state.authData.isAuth
        }),
        {
            updateSearchData,
            toggleFollow,
            fetchUsers
        }
    )
)(UsersAPIContainer)

