import React from 'react';
import s from './Users.module.scss';
import ListItem from './ListItem'
import Pagination from '../common/Pagination'
import Preloader from '../common/Preloader'
import Search from './Search';
import {objectToQueryString} from '../../utils/helpers/object'

export default props => {
    // if(!props.users.length) return <Preloader loading={true}/>;

    const {
        loading,
        users,
        searchData,
        ...other
    } = props;

    const {term , count} = searchData;

    const setPage = page => {
        props.history.push('/users?'+objectToQueryString({page, count, term}));
    };
    const runSearch = term => {
        props.history.push('/users?' + objectToQueryString({page: 1, count, term}));
    };

    return (
        <div className={s.Users} key={'Users'}>
            <div className={s.title}>Users</div>

            <Search term={term} runSearch={runSearch}/>

            {!props.users.length && <div>Не найдены</div>}

            <Preloader loading={loading}/>

            <ul className={s.list + (loading ? ' ' + s.loading: '')}>
                {users.map(user => ListItem({user, ...other}))}
            </ul>

            {props.users.length > 0 && <Pagination {...searchData} onClickHandler={setPage}/>}

        </div>
    );
}

